.question {
  margin: 0; }
  .question .wrapper {
    padding: 7rem 0; }
    @media (min-width: 1px) and (max-width: 620px) {
      .question .wrapper {
        height: auto;
        padding: 5rem 1rem; } }
    .question .wrapper input[type=radio] {
      display: none; }
  .question .questionHeadingContainer--1 {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    @media (min-width: 1px) and (max-width: 620px) {
      .question .questionHeadingContainer--1 {
        flex-direction: column; } }
    .question .questionHeadingContainer--1 p {
      width: 60%;
      font-size: 2.6rem;
      color: #FFF; }
      @media (min-width: 1px) and (max-width: 620px) {
