    border-radius: 50%;
    background-color: #FFF;
    order: -1; }
  .question .avatarContainer--blue {
    border: 1.2rem solid #EC6333; }
    @media (min-width: 1px) and (max-width: 620px) {
      .question .avatarContainer--blue {
